var Sec_index;
var Sec_Title=[];//标题数组
var Sec_Content=[];//正文数组

export default {
	data() {
		return {
			
		}
	},
	methods: {
		det(){
			uni.navigateTo({
				url:'../det/det'
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor.2"
			})
		}
	},
	onLoad() {
		Sec_Title=[];//标题数组
		Sec_Content=[];//正文数组

		uni.getStorage({
			key:"PJ_ID",
			success: (res) => {
				Sec_index = res.data;
				console.log(Sec_index);
				for (var i = 1;i <= Sec_index ;i++) {
					uni.getStorage({
						key:i + "PSec_Title",
						success: (res) => {
							Sec_Title.push(res.data);
							console.log(Sec_Title);
							}
						})
					uni.getStorage({
						key:i + "PSec_Content",
						success: (res1) => {
							Sec_Content.push(res1.data);
							console.log(Sec_Content)
						}
					})
				}
			}
		})
	}
}
